:root {
    --bg: #222;
    --panel: #2b2b2b;
    --panel-2: #333;
    --text: #e8e8e8;
    --accent: #a00019;
    --grid: #565656;
    --tick: #a0a0a0;
    --btn-bg: #4a4a4a;
    --btn-bg-hover: #5a5a5a;
    --btn-text: #f1f1f1;
    --sep: #bfbfbf22;
}

* { box-sizing: border-box; }
html, body { height: 100%; }
body {
    margin: 0;
    background: var(--bg);
    color: var(--text);
    font: 14px/1.4 system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
}

.app { max-width: 1080px; margin: 16px auto; padding: 12px; }

.toolbar { display: flex; gap: 12px; margin-bottom: 8px; align-items: center; }
.toolbar button {
    border: 0; border-radius: 6px; padding: 8px 12px;
    background: var(--btn-bg); color: var(--btn-text); cursor: pointer;
}
.toolbar button:hover { background: var(--btn-bg-hover); }
.toolbar .spacer { flex: 1; }
.toolbar .hint { opacity: .8; font-size: 12px; }

.zone { background: var(--panel); border-radius: 6px; padding: 8px; border: 1px solid var(--sep); }
.zone + .zone { margin-top: 10px; }

svg { display: block; width: 100%; height: 260px; background: var(--panel-2); border-radius: 4px; }

.header { display: flex; justify-content: space-between; align-items: center; color: #cfcfcf; margin: 4px 2px 8px; font-weight: 600; }

.buffer svg { background: #2e2e2e; }
.work   svg { background: #2e2e2e; }

.badge { font-size: 12px; opacity: .8; }

.grid-line { stroke: var(--grid); stroke-width: 1; vector-effect: non-scaling-stroke; }
.axis-text { fill: var(--tick); font-size: 10px; user-select: none; }
.poly      { fill: var(--accent); cursor: grab; }
.poly:active { cursor: grabbing; }

.drop-highlight { outline: 2px solid #d0d0d0; outline-offset: 2px; }
